import ClientHome from '../components/ClientHome';
import Project from '../models/Project';
import connectToDatabase from '../lib/db';

// Force dynamic behavior so it re-fetches on request? Or revalidate periodically.
// For portfolio, maybe caching 60 seconds is fine.
export const revalidate = 60;

async function getProjects() {
    try {
        await connectToDatabase();
        // Plain object for serialization
        const projects = await Project.find({}).sort({ order: 1, createdAt: -1 }).lean();

        // Convert _id to string manually if needed, but lean() usually helps.
        // However, Next.js passing object from Server to Client needs plain JSON.
        // Mongoose _id is an object.
        return JSON.parse(JSON.stringify(projects));
    } catch (error) {
        console.error("Failed to fetch projects:", error);
        return [];
    }
}

export default async function Home() {
    const projects = await getProjects();
    return <ClientHome projects={projects} />;
}
